<template>
  <div>
    <div>
      <div style="height:400px; overflow:auto;">
        <div style="background : gold; font-size:14px; font-weight:bold;"> JOB / </div>
        <table style=" margin : auto; width:97%; ">
          <colgroup>
            <col style="width: 50px"> <!-- ID -->
            <col style="width: 80px"> <!-- Schema ID -->
            <col style="width: 120px"> <!-- Name -->
            <col style="width: 200px"> <!-- comment -->
            <col style="width: 70px"> <!-- detail -->
            <col style="width: 70px"> <!-- distribute -->
            <col style="width: 70px"> <!-- delete -->
            <col style="width: 70px"> <!-- run -->
            <col style="width: 70px"> <!-- kill -->
          </colgroup>
          <tr>
            <th class="svrMngColName">ID</th>
            <th class="svrMngColName"> ID</th>
            <th class="svrMngColName">Job Name</th>
            <th class="svrMngColName">Comment</th>
            <th class="svrMngColName"></th>
            <th class="svrMngColName"></th>
            <th class="svrMngColName"></th>
            <th class="svrMngColName"></th>
            <th class="svrMngColName"></th>
          </tr>

          <tr v-for="p in jobArray" :key="p.id">
            <td>{{ p.id }}</td>
            <td>{{ p.schema_id }}</td>
            <td>{{ p.name }}</td>
            <td>{{ p.comment }}</td>
            <td> <button v-on:click="jobDetailPopup(p.id)">  </button> </td>
            <td> <span class="removeBtn" type="button" v-on:click=""> Dist </span></td>
            <td> <button v-on:click="removeJob(p.id)">  </button> </td>
            <!-- <td> <span class="removeBtn" type="button" v-on:click="removeJob(p.id)"> Del </span> </td> -->
            <td> <span class="removeBtn" type="button" v-on:click=""> RUN </span> </td>
            <td> <span class="removeBtn" type="button" v-on:click=""> KILL </span> </td>
          </tr>
        </table>
      </div>


      <table style="margin:auto; width:400px;">

        <tr>
          <td style="width : 90px;" class="add_title"> Schema ID </td>
          <td>
            <select style="width : 95%;" v-model="add_jobSchema">
              <option v-for="(item, index) in schemaArray" :value="item.id"> {{ item.id }} - {{ item.name }} </option>
            </select>
          </td>
        </tr>
        <tr>
          <td style="width : 90px;" class="add_title"> Name </td>
          <td> <input style="width : 95%;" type="text" v-model="add_jobName" v-on:keyup.enter="addJob"> </td>
        </tr>

        <tr>
          <td style="width : 90px;" class="add_title"> Comment </td>
          <td> <input style="width : 95%;" type="text" v-model="add_jobComment" v-on:keyup.enter="addJob"> </td>
        </tr>

      </table>
      <button v-on:click = "addJob" >  </button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import serverConfig from "../assets/data/server_config.json";

export default {
  data() {
    return {
      schemaArray : [],
      svrConfig : serverConfig,
      svrAddr : '',
      jobArray : [],
      add_jobName : '',
      add_jobSchema : '',
      add_jobComment : '',
      openWin : ''
    }
  },
  created() {
    this.svrAddr = this.svrConfig.hostserver;
    this.loadJob();
    this.loadSchema();
  },

  methods: {
    loadSchema() {
      console.log("**** LOAD SCHEMA2 ****");
      var api = "http://" + this.svrAddr + ":3000/users/cell_schemas";
      axios
      .get(api)
      .then(response => {
        console.log("22222");
        console.log(response.data[0].id);
        //console.log("222" + response);
        this.schemaArray = response.data;
      })
      .catch(err => {
        console.log(err);
      });
      console.log("END");
      console.log("");

    },

    jobDetailPopup(index) {
      window.name = "parentForm";
      var url = "taskdetail?jobID=" + index;
      this.openWin = window.open(url, "childForm", "width=1250, height=1000, resizable = no, scrollbars = no");

      console.log("click popup");
      this.openWin.document.title = "SIEVE2 Job Detail";
      this.openWin.document.getElementById("job_id").value = index;
    },
    removeJob(index) {
      console.log("DELETE" + index);
      var params = {
        id : index
      }

      var api = "http://" + this.svrAddr + ":3000/users/jobs/delete";

      axios
      .post(api, params)
      //.post('http://165.132.145.49:3000/users/jobs/delete', params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.loadJob();
      this.loadJob();
    },

    addJob() {
      console.log("add job");
      console.log("params : " + this.add_jobName + this.add_jobSchema + this.add_jobComment);
      var params = {
        name: this.add_jobName,
        schema_id: this.add_jobSchema,
        comment: this.add_jobComment
      }

      var api = "http://" + this.svrAddr + ":3000/users/jobs";

      axios
      .post(api, params)
      //.post('http://165.132.145.49:3000/users/jobs', params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.clearInput();
      this.loadJob();
      this.loadJob();
      this.loadJob();
    },

    loadJob() {
      console.log("**** LOAD Job ****");
      var api = "http://" + this.svrAddr + ":3000/users/jobs";
      axios
      .get(api)
      //.get('http://165.132.145.49:3000/users/jobs')
      //.get('http://165.132.105.40:3000/users/jobs')
      .then(response => {
        console.log(response);
        this.jobArray = response.data;
      })
      .catch(err => {
        console.log(err);
      });
    },
    clearInput() {
      this.add_jobName = "";
      this.add_jobSchema = "";
      this.add_jobComment = "";
    }

  }
}
</script>

<style scoped>


</style>



