<template>
  <div>
    <div>
      <div style="height:400px; overflow:auto;">
        <div style="background : gold; font-size:14px; font-weight:bold;"> Task / </div>
        <table style=" margin : auto; width:97%; ">
          <colgroup>
            <col style="width: 50px"> <!-- ID -->
            <col style="width: 80px"> <!-- name -->
            <col style="width: 80px"> <!-- program type -->
            <col style="width: 150px"> <!-- comment -->
            <col style="width: 50px"> <!-- delete -->
          </colgroup>
          <tr>
            <th class="svrMngColName">ID</th>
            <th class="svrMngColName">Name</th>
            <th class="svrMngColName">Program Type</th>
            <th class="svrMngColName">Comment</th>
            <th class="svrMngColName">Del</th>
          </tr>

          <tr v-for="p in taskArray" :key="p.id">
            <td>{{ p.id }}</td>
            <td>{{ p.name }}</td>
            <td>{{ p.program_type }}</td>
            <td>{{ p.comment }}</td>
            <td> <span class="removeBtn" type="button" v-on:click="removeTask(p.id)"> Del </span> </td>
          </tr>
        </table>
      </div>


      <table style="margin:auto; width:400px;">
        <tr>
          <td style="width : 100px;" class="add_title"> Name </td>
          <td> <input style="width : 95%;" type="text" v-model="add_taskName" v-on:keyup.enter="addTask"> </td>
        </tr>

        <tr>
          <td style="width : 90px;" class="add_title"> Program Type </td>
          <td>
            <select style="width : 95%; border:1px solid;" v-model="add_taskProg">
              <option value = "0"> python </option>
              <option value = "1"> java </option>
              <option value = "2"> node </option>
              <option value = "3"> sieve </option>
            </select>
           </td>
        </tr>

        <tr>
          <td style="width : 90px;" class="add_title"> Comment </td>
          <td> <input style="width : 95%;" type="text" v-model="add_taskComment" v-on:keyup.enter="addTask"> </td>
        </tr>

        <tr>
          <td style="width : 90px;" class="add_title"> Comment </td>
          <td> <input type="file" style="width:95%;"> </td>
        </tr>

      </table>
        <button v-on:click = "addTask" >  </button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import serverConfig from "../assets/data/server_config.json";

export default {
  data() {
    return {
      svrConfig : serverConfig,
      svrAddr : '',
      taskArray : [],
      add_taskName : '',
      add_taskProg : '',
      add_taskComment : ''
    }
  },
  created() {
    this.svrAddr = this.svrConfig.hostserver;
    this.loadTask();
  },
  methods: {
    addTask() {
      var params = {
        name: this.add_taskName,
        program_type: this.add_taskProg,
        comment: this.add_taskComment
      }

      var api = "http://" + this.svrAddr + ":3000/users/tasks";

      axios
      .post(api, params)
      //.post('http://165.132.145.49:3000/users/tasks', params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.clearInput();
      this.loadTask();
      this.loadTask();
      this.loadTask();
    },

    clearInput() {
      this.add_taskName = "";
      this.add_taskProg = "";
      this.add_taskComment = "";
    },

    removeTask(index) {
      console.log("DELETE" + index);
      var params = {
        id : index
      }

      var api = "http://" + this.svrAddr + ":3000/users/tasks/delete";

      axios
      .post(api, params)
      //.post('http://165.132.145.49:3000/users/tasks/delete', params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.loadTask();
      this.loadTask();
    },

    loadTask() {
      console.log("**** LOAD Task ****");
      var api = "http://" + this.svrAddr + ":3000/users/tasks";
      axios
      .get(api)
      //.get('http://165.132.145.49:3000/users/tasks')
      //.get('http://165.132.105.40:3000/users/tasks')
      .then(response => {
        console.log(response);
        this.taskArray = response.data;
      })
      .catch(err => {
        console.log(err);
      });
    }
  }
}

</script>

<style scoped>

</style>



