<template>
  <div>
    <div style="height:400px; overflow:auto;">
      <div style="background : gold; font-size:14px; font-weight:bold;">   </div>
      <table style=" margin : auto; width:95%;">
        <colgroup>
          <col style="width: 50px"> <!-- ID -->
          <col style="width: 120px"> <!-- IP -->
        	<col style="width: 120px"> <!-- root ID -->
        	<col style="width: 120px"> <!-- root password -->
        	<col style="width: 120px"> <!-- sieve key -->
          <col style="width: 80px"> <!-- delete -->
        </colgroup>
        <tr>
          <th class="svrMngColName">ID</th>
          <th class="svrMngColName">IP ADDR</th>
          <th class="svrMngColName">Root ID</th>
          <th class="svrMngColName">Root PW</th>
          <th class="svrMngColName">SIEVE KEY</th>
          <th class="svrMngColName">DEL</th>
        </tr>

        <tr v-for="p in pageArray" :key="p.id">
          <td>{{ p.id }}</td>
          <td>{{ p.ip_address }}</td>
          <td>{{ p.root_id }}</td>
          <td>{{ p.root_passwd }}</td>
          <td>{{ p.sieve_key }}</td>
          <td>
            <span class="removeBtn" type="button" v-on:click="removeSvr(p.id)"> Del </span>
          </td>
        </tr>
      </table>
    </div>
    <table style="margin:auto; width:600px;">
      <tr>
        <td style="width : 90px;" class="add_title"> IP ADDR </td>
        <td> <input style="width : 95%;" type="text" v-model="add_ip" v-on:keyup.enter="addSvr"> </td>
        <td style="width : 90px;" class="add_title"> Root ID </td>
        <td> <input style="width : 95%;" type="text" v-model="add_id" v-on:keyup.enter="addSvr"> </td>
      </tr>
      <tr>
        <td style="width : 90px;" class="add_title"> Root PW </td>
        <td> <input style="width : 95%;" type="text" v-model="add_pw" v-on:keyup.enter="addSvr"> </td>
        <td style="width : 90px;" class="add_title"> SIEVE KEY </td>
        <td> <input style="width : 95%;" type="text" v-model="add_sievekey" v-on:keyup.enter="addSvr"> </td>
      </tr>
    </table>
    <button v-on:click="addSvr">  </button>
    <button v-on:click="clearSvr">   </button>
  </div>
</template>

<script>
import axios from 'axios'
import userList from "../assets/data/users.json";
import serverConfig from "../assets/data/server_config.json";

export default {
  name: 'SvrManage',
  data () {
    return {
      user : userList,
      svrConfig : serverConfig,
      svrAddr : '',
      pageArray: [],
      add_ip : '',
      add_id : '',
      add_pw : '',
      add_sievekey : ''
    }
  },
  created () {
    this.svrAddr = this.svrConfig.hostserver;
    this.loadSvr();
  },

  methods: {
    loadSvr() {
      console.log("**** LOAD SERVER ****");
      console.log("addr : " + this.svrAddr);
      var api = "http://" + this.svrAddr + ":3000/users/engine_computer";
      axios
      .get(api)
      .then(response => {
        console.log(response);
        this.pageArray = response.data;
      })
      .catch(err => {
        console.log(err);
      });
    },

    addSvr() {
      var value_ip = this.add_ip && this.add_ip.trim();
      var value_id = this.add_id && this.add_id.trim();
      var value_pw = this.add_pw && this.add_pw.trim();
      var value_sievekey = this.add_sievekey && this.add_sievekey.trim();
      var params = {
        ip_address: value_ip,
        root_id: value_id,
        root_passwd: value_pw,
        sieve_key: value_sievekey
      }

      var api = "http://" + this.svrAddr + ":3000/users/engine_computer";

      axios
      .post(api, params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.clearInput();
      this.loadSvr();
      this.loadSvr();
      this.loadSvr();
    },

    clearSvr() {
      console.log("CLEAR SERVER");
      var api = "http://" + this.svrAddr + ":3000/users/engine_computer/delete_all";
      axios
      .post(api)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );
      this.loadSvr();
      this.loadSvr();
      this.loadSvr();
    },

    removeSvr(index) {
      console.log("DELETE" + index);
      var params = {
        id : index
      }
      var api = "http://" + this.svrAddr + ":3000/users/engine_computer/delete";
      axios
      .post(api, params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.loadSvr();
      this.loadSvr();
    },

    clearInput() {
      this.add_ip = "";
      this.add_id = "";
      this.add_pw = "";
      this.add_sievekey = "";
    }
  }
}
</script>

<style>
h1 { color: #454545; text-align: center; }
.svrMngColName { background : lightblue; }
</style>



