<template>
  <div>
    <div style="height:500px; overflow:auto;">
      <div style="background : gold; font-size:14px; font-weight:bold;">   </div>
      <div style="width:48%; height:450px; float:left;">
        <div style="background : grey; color:white; font-size:14px; font-weight:bold;">   </div>
        <div style="height:350px;">
          <table style=" margin : auto; width:97%;">
            <colgroup>
              <col style="width: 50px"> <!-- ID -->
              <col style="width: 120px"> <!-- IP -->
            	<col style="width: 120px"> <!-- root ID -->
              <col style="width: 50px"> <!-- root ID -->
              <col style="width: 50px"> <!-- root ID -->
            </colgroup>
            <tr>
              <th class="svrMngColName">ID</th>
              <th class="svrMngColName">NAME</th>
              <th class="svrMngColName">Comment</th>
              <th class="svrMngColName">DEL</th>
              <th class="svrMngColName">Col</th>
            </tr>
            <tr v-for="p in schemaArray" :key="p.id">
              <td>{{ p.id }}</td>
              <td>{{ p.name }}</td>
              <td>{{ p.comment }}</td>
              <td> <span class="removeBtn" type="button" v-on:click="removeSchema(p.id)"> Del </span> </td>
              <td> <span class="removeBtn" type="button" v-on:click="loadSchemaColumn(p.id)"> Col </span> </td>
            </tr>
          </table>
        </div>
        <div style="height:100px;">
          <table style="margin:auto; width:90%;">
            <tr>
              <td style="width : 70px;" class="add_title"> NAME </td>
              <td> <input style="width : 95%;" type="text" v-model="add_schemaName" v-on:keyup.enter="addSchema"> </td>
            </tr>
            <tr>
              <td style="width : 70px;" class="add_title"> Comment </td>
              <td> <input style="width : 95%;" type="text" v-model="add_schemaCom" v-on:keyup.enter="addSchema"> </td>
            </tr>
          </table>
          <button v-on:click="addSchema">   </button>
        </div>
      </div>


      <div style="width:52%; height:450px; float:left;">
        <div style="background : grey; color:white; font-size:14px; font-weight:bold;">   </div>
        <div style="height:350px;">
          <table style=" margin : auto; width:97%; ">
            <colgroup>
              <col style="width: 50px"> <!-- ID -->
              <col style="width: 70px"> <!-- sch ID -->
              <col style="width: 150px"> <!-- name -->
            	<col style="width: 80px"> <!-- type ID -->
              <col style="width: 50px"> <!-- del -->
            </colgroup>
            <tr>
              <th class="svrMngColName">ID</th>
              <th class="svrMngColName">Schema</th>
              <th class="svrMngColName">NAME</th>
              <th class="svrMngColName">Type</th>
              <th class="svrMngColName">DEL</th>
            </tr>
            <tr v-for="p in columnArray" :key="p.id">

              <td>{{ p.id }}</td>
              <td>{{ p.schema_id }}</td>
              <td>{{ p.name }}</td>
              <td>{{ p.type_id }}</td>
              <td> <span class="removeBtn" type="button" v-on:click="removeColumn(p.id, p.schema_id)"> Del </span> </td>
            </tr>
          </table>
        </div>
        <div style="height:100px;">
          <table style="margin:auto; width:90%;">
            <tr>
              <td style="width : 70px;" class="add_title"> Schema ID </td>
              <td> <div v-model="add_ColSchema"> {{ add_ColSchema }} </div> </td>
              <!--
              <td> <input style="width : 95%;" type="text" v-model="add_ColSchema" v-on:keyup.enter="addSchema"> </td>
              -->
            </tr>

            <tr>
              <td style="width : 70px;" class="add_title"> NAME </td>
              <td> <input style="width : 95%;" type="text" v-model="add_ColName" v-on:keyup.enter="addSchema"> </td>
            </tr>

            <tr>
              <td style="width : 70px;" class="add_title"> Type ID </td>
              <td> <input style="width : 95%;" type="text" v-model="add_ColType" v-on:keyup.enter="addSchema"> </td>
            </tr>
          </table>
          <button v-on:click="addColumn">   </button>
        </div>
      </div>



    </div>
  </div>
</template>

<script>
import axios from 'axios'
import serverConfig from "../assets/data/server_config.json";

export default {
  name: 'SchemaManage',
  data () {
    return {
      svrConfig : serverConfig,
      svrAddr : '',
      schemaArray: [],
      columnArray: [],
      add_schemaName : '',
      add_schemaCom : '',
      add_ColName : '',
      add_ColSchema : '',
      add_ColType : ''
    }
  },
  created () {
    this.svrAddr = this.svrConfig.hostserver;
    this.loadSchema();

  },
  methods: {
    loadSchemaColumn(index) {
      console.log("**** LOAD SCHEMA COLUMN ****" );
      this.add_ColSchema = index;
      console.log(index);
      var params = {
        schema_id: index
      }
      var api = "http://" + this.svrAddr + ":3000/users/cell_columns";
      axios
      .post(api, params)
      .then( response => {
        console.log(response);
        this.columnArray = response.data;
      })
      .catch( response => { console.log(response) } );
    },
    addColumn() {
      console.log("ADD COLUMN");
      var params = {
        name: this.add_ColName,
        schema_id: this.add_ColSchema,
        type_id: this.add_ColType
      }

      var api = "http://" + this.svrAddr + ":3000/users/cell_columns/add";

      axios
      .post(api, params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.clearInput();
    },
    removeColumn(index, schema_id) {
      console.log("DELETE" + index);
      var params = {
        id : index
      }

      var api = "http://" + this.svrAddr + ":3000/users/cell_columns/delete";

      axios
      .post(api, params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.loadSchemaColumn(schema_id);
      this.loadSchemaColumn(schema_id);
      this.loadSchemaColumn(schema_id);
    },

    loadSchema() {
      console.log("**** LOAD SCHEMA ****");
      var api = "http://" + this.svrAddr + ":3000/users/cell_schemas";
      axios
      .get(api)
      .then(response => {
        console.log(response);
        this.schemaArray = response.data;
      })
      .catch(err => {
        console.log(err);
      });
    },
    addSchema() {
      console.log("ADD SCHEMA");
      var params = {
        name: this.add_schemaName,
        comment: this.add_schemaCom
      }

      var api = "http://" + this.svrAddr + ":3000/users/cell_schemas";

      axios
      .post(api, params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.clearInput();
      this.loadSchema();
      this.loadSchema();
      this.loadSchema();
    },
    removeSchema(index) {
      console.log("DELETE" + index);
      var params = {
        id : index
      }

      var api = "http://" + this.svrAddr + ":3000/users/cell_schemas/delete";

      axios
      .post(api, params)
      .then( response => { console.log(response) } )
      .catch( response => { console.log(response) } );

      this.loadSchema();
      this.loadSchema();
    },
    clearInput() {
      this.add_schemaCom = "";
      this.add_schemaName = "";
      this.add_ColName = "",
      this.add_ColSchema = "",
      this.add_ColType = ""
    }
  }
}
</script>

<style>
h1 { color: #454545; text-align: center; }
.svrMngColName { background : lightblue; }
</style>



