function addlink(start_x, start_y, dest_x, dest_y, w1, h1, w2, h2) {
  var startpt_x = 0, startpt_y=0, destpt_x=0, destpt_y=0;
  var box_margin = 2;
  var s_top = start_y - box_margin;
  var d_top = dest_y - box_margin;
  var s_bottom = start_y + h1 + box_margin;
  var d_bottom = dest_y + h2 + box_margin;
  var s_left = start_x - box_margin;
  var d_left = dest_x - box_margin;
  var s_right = start_x + w1 + box_margin;
  var d_right = dest_x + w2 + box_margin;

  var r__x, r__y, l__x, l__y;
  var m__x, m__y;
  var degree, v_degree;
  var _size;
  var c;
  var step = 1;
  var _dif;
  var _len;

  if(s_top >= d_bottom) {
    if(s_left >= (d_right + w2)) {
      ////console.log("case 1");
      startpt_x = s_left;
      startpt_y = s_top + h1/2;
      destpt_x = d_right;
      destpt_y = d_top + h2/2;
    }
    else if((s_right+w1) <= d_left) {
      ////console.log("case 2");
      startpt_x = s_right;
      startpt_y = s_top + h1/2;
      destpt_x = d_left;
      destpt_y = d_top + h2/2;
    }
    else {
      ////console.log("case 3");
      startpt_x = s_left + w1/2;
      startpt_y = s_top;
      destpt_x = d_left + w2/2;
      destpt_y = d_bottom;
    }
  }
  else if(s_bottom <= d_top) {
    if(s_left >= (d_right+w2)) {
      ////console.log("case 4");
      startpt_x = s_left;
      startpt_y = s_top + h1/2;
      destpt_x = d_right;
      destpt_y = d_top + h2/2;
    }
    else if((s_right+w1) <= d_left) {
      ////console.log("case 5");
      startpt_x = s_right;
      startpt_y = s_top + h1/2;
      destpt_x = d_left;
      destpt_y = d_top + h2/2;
    }
    else {
      ////console.log("case 6");
      startpt_x = s_left + w1/2;
      startpt_y = s_bottom;
      destpt_x = d_left + w2/2;
      destpt_y = d_top;
    }
  }
  else{
    if(s_left >= d_right) {
      ////console.log("case 7");
      startpt_x = s_left;
      startpt_y = s_top + h1/2;
      destpt_x = d_right;
      destpt_y = d_top + h2/2;
    }
    else if(s_right <= d_left) {
      ////console.log("case 8");
      startpt_x = s_right;
      startpt_y = s_top + h1/2;
      destpt_x = d_left;
      destpt_y = d_top + h2/2;
    }
  }

  degree = (destpt_y - startpt_y) / (destpt_x - startpt_x);

  if(degree == "Infinity") {
    //console.log("Infini");
    degree = 1000;
  }
  if(degree == "-Infinity")
  {
    degree = -1000
  }
  if(degree == 0) {
    degree = 0.001;
  }
  v_degree = -1 / degree;
  _len = 5;

  if(destpt_x >= startpt_x) {
    c = 1;
  }
  else{
    c = 2;
  }
  ////console.log(" case : " + c);

  m__x = pointing_x(destpt_x, destpt_y, degree, _len, c, step);
  m__y = pointing_y(m__x, destpt_x, destpt_y, degree);

  step = 1;
  r__x = pointing_x(m__x, m__y, v_degree, _len, c, step);
  r__y = pointing_y(r__x, m__x, m__y, v_degree);

  step = 2;
  l__x = pointing_x(m__x, m__y, v_degree, _len, c, step);
  l__y = pointing_y(l__x, m__x, m__y, v_degree);


  var svgdiv = document.getElementById('arrowbg');
  var path = document.createElementNS('http://www.w3.org/2000/svg', 'path');
  path.setAttribute('id', 'linkid');
  path.setAttribute('class', 'linkline');
  path.setAttribute('d', "M" + startpt_x + "," + startpt_y + " " + destpt_x + "," + destpt_y + " " + r__x + "," + r__y + " " + l__x + "," + l__y + " " + destpt_x + "," + destpt_y);
  svgdiv.appendChild(path);
}
function pointing_x(a, b, d, l, c, step) {
  var x1, x2, result;
  var A = 1 + (d*d);
  var B = (-2*a*d*d) - (2*a);
  var C = (a*a) + (a*a*d*d) - (l*l);

  x1 = (-1*B + Math.sqrt(B*B - 4*A*C)) / (2*A);
  x2 = (-1*B - Math.sqrt(B*B - 4*A*C)) / (2*A);

  if(c == 1) {
    if(step == 1) { result = Math.min(x1, x2); }
    else { result = Math.max(x1, x2); }
  }
  else {
    if(step == 1) { result = Math.max(x1, x2); }
    else { result = Math.min(x1, x2); }
  }
  return result;
}
function pointing_y(x, a, b, d) {
  var result;
  result = d * x + b - a*d;
  return result;
}

