          config : arr[7],
          heartbeat_task_id : arr[8],
          heartbeat_job_id : arr[9],
          output_schema_id : arr[10],
          position_x : arr[11],
          position_y : arr[12],
          name : arr[13]
        }

        var api = "http://" + this.svrAddr + ":3000/users/job_tasks/add";

        axios
        .post(api, params)
        //.post('http://165.132.145.49:3000/users/job_tasks/add', params)
        .then( response => {
          ////console.log(response);
        })
        .catch( response => { console.log(response) } );
      }
      alert("Saved");
    },
    loadTask(index) {
      console.log("load Task called");
      console.log("index : " + index);
      var params = { job_id: index }
      var api = "http://" + this.svrAddr + ":3000/users/job_tasks";
      axios
      .post(api, params)
      .then( response => {
        this.taskArray = response.data;
        console.log(response.data);
        this.taskcnt = response.data.length;
        this.initDIV();
        this.createLine();
      })
      .catch( response => { console.log(response) } );
    },
    initDIV() {
      for(var i=0; i<this.taskcnt; i++) {
        this.createDIV(i);
        this.localize(
          this.taskArray[i].id,
          this.taskArray[i].job_id,
          this.taskArray[i].task_id,
          this.taskArray[i].input_schema_id,
          this.taskArray[i].listening_port,
          this.taskArray[i].ec_id,
          this.taskArray[i].output_type,
          this.taskArray[i].config,
          this.taskArray[i].heartbeat_task_id,
          this.taskArray[i].heartbeat_job_id,
          this.taskArray[i].output_schema_id,
          this.taskArray[i].position_x,
          this.taskArray[i].position_y,
          this.taskArray[i].linkto,
          this.taskArray[i].name
        );
      }
      this.taskcnt = 0;
    },
    createLine() {
      var output, arr;
      var start_x, start_y, dest_x, dest_y;
      localStorage.removeItem("loglevel:webpack-dev-server");
      for(var i=0; i<localStorage.length; i++) {
        output = localStorage.getItem(i);
        arr = JSON.parse(output);
        if(arr[13] != null) {
          localStorage.removeItem("loglevel:webpack-dev-server");
          //console.log("");
          //console.log("Start : " + arr[0]);
          //console.log("End : " + arr[13]);
          //console.log("");
          start_x = this.get_position_x(arr[0]);
          start_y = this.get_position_y(arr[0]);
          dest_x = this.get_position_x(arr[13]);
          dest_y = this.get_position_y(arr[13]);
        }
        addlink(start_x, start_y, dest_x, dest_y, 90, 90, 90, 90);
      }
    },
    get_position_x(index) {
      var output, arr;
      var localkey;
      var position;

      localStorage.removeItem("loglevel:webpack-dev-server");

      for(var i=0; i<localStorage.length; i++) {
        output = localStorage.getItem(i);
        arr = JSON.parse(output);
        if(arr[0] == index) {
          position = arr[11];
        }
      }
      return position;
    },
    get_position_y(index) {
      var output, arr;
      var localkey;
      var position;

      localStorage.removeItem("loglevel:webpack-dev-server");

      for(var i=0; i<localStorage.length; i++) {
        output = localStorage.getItem(i);
        arr = JSON.parse(output);
        if(arr[0] == index) {
          position = arr[12];
        }
      }
      return position;
    },
    createDIV(index) {
      var obj = document.getElementById("content");
      var newDIV = document.createElement("div");
      var id = this.taskArray[index].id;
      var name = this.taskArray[index].name;
      var marginLeft = this.taskArray[index].position_x;
      var marginTop = this.taskArray[index].position_y;
      var position = "margin-left : " + marginLeft + "px; margin-top : " + marginTop + "px;";

      newDIV.setAttribute("id", id);
      newDIV.setAttribute('class', "div-content");
      newDIV.innerHTML = "<table> <tr> <p class='no-drag'>" + name + "</p> </tr> <tr> <p class='no-drag2'>" + id + "</p> </tr> </table>";
      newDIV.setAttribute("style", position);
      obj.appendChild(newDIV);
    },
    createDIV2() {
      var id = this.tempID;
      var name;
      var obj = document.getElementById("content");
      var newDIV = document.createElement("div");

      /*

      switch (this.task_type){
        case "0" : name = "Transact"; break;
        case "1" : name = "Select"; break;
        case "2" : name = "BaseStream"; break;
        case "3" : name = "B Join"; break;
        case "4" : name = "Aggregation"; break;
        case "5" : name = "Expression"; break;
        case "6" : name = "Projection"; break;
        case "7" : name = "Union"; break;
        case "8" : name = "Soc Output"; break;
        case "9" : name = "Half Join +"; break;
        case "10" : name = "Split"; break;
        case "11" : name = "Final"; break;
        case "12" : name = "M Join"; break;
        case "13" : name = "Merge Job"; break;
        case "14" : name = "Kospi Index"; break;
        case "15" : name = "Period"; break;
        case "16" : name = "Velocity"; break;
        case "17" : name = "TopK"; break;
        case "18" : name = "1 Item Freq"; break;
        case "19" : name = "Freq ItemSet"; break;
        case "20" : name = "Context"; break;
        case "21" : name = "Clustering"; break;
        case "22" : name = "Assoc Rule"; break;
        case "23" : name = "Seq Pattern"; break;
        case "24" : name = "Context Rule"; break;
        case "25" : name = "Context Seq"; break;
        default :
        name = "undefined"
      }
      */

      newDIV.setAttribute("id", id);
      newDIV.setAttribute('class', "div-content");
      newDIV.innerHTML = "<table> <tr> <p class='no-drag'> " + name + " </p> </tr> <tr> <p class='no-drag2'> " + id + " </p> </tr> </table>";

      obj.appendChild(newDIV);
      this.localize(
        id,
        this.jobId,
        this.task_id,
        this.input_schema,
        this.listening_port,
        this.ec_id,
        this.output_type,
        this.config,
        this.heartbeat_task,
        this.heartbeat_job,
        this.output_schema,
        0,
        0,
        null,
        name
      );
      this.tempID += 1;
      this.clearInput();

    },
    localize(id, job_id, task_id, input_schema, listening_port, ec_id, output_type, config, heartbeat_task, heartbeat_job, output_schema, position_x, position_y, linkto, name) {
      localStorage.removeItem("loglevel:webpack-dev-server");
      var localLength = localStorage.length;
      var arr = [id, job_id, task_id, input_schema, listening_port, ec_id, output_type, config, heartbeat_task, heartbeat_job, output_schema, position_x, position_y, linkto, name];
      localStorage.setItem(localLength, JSON.stringify(arr));
      this.localcnt = localLength + 1;
    },
    clearInput() {
      //this.task_type = '',
      this.task_id = '',
      this.input_schema = '',
      this.listening_port = '',
      this.ec_id = '',
      this.output_type = '',
      this.config = '',
      this.heartbeat_task = '',
      this.heartbeat_job = '',
      this.output_schema = ''
    }
  }
}
</script>

<style src="./css/total.css"></style>



